import axios from 'axios'
export default {
  name: 'checkProjectDetailForm',
  data () {
    return {
      chooseMissionId: '',
      taskState: '',
      projectID: '',
      task: '',
      okTask: false,
      addMission: false,
      editMission: false,
      deleteMission: false,
      studentList: [],
      projectName: '',
      name: '',
      startTime: '',
      endTime: '',
      content: '',
      target: '',
      leaderName: '',
      mission: {
        name: '',
        moduleID: parseInt(sessionStorage.getItem('taskID')),
        startTime: '',
        endTime: '',
        ownerID: '',
        content: '',
        target: ''
      },
      editMissionForm: {
        name: '',
        moduleID: parseInt(sessionStorage.getItem('taskID')),
        startTime: '',
        endTime: '',
        ownerID: '',
        content: '',
        target: ''
      },
      missionList: [{
        name: '',
        moduleID: parseInt(sessionStorage.getItem('taskID')),
        startTime: '',
        endTime: '',
        ownerID: '',
        content: '',
        target: ''
      }]
    }
  },
  methods: {
    handleAddMission () {
      this.getStudentList()
      this.mission.name = ''
      this.mission.startTime = ''
      this.mission.endTime = ''
      this.mission.content = ''
      this.mission.target = ''
      this.mission.ownerID = -1
      this.addMission = true
    },
    getTask () {
      axios.get(this.url + '/module/id/' + sessionStorage.getItem('taskID'))
        .then(this.getTaskSucc)
    },
    getTaskSucc (res) {
      res = res.data
      if (res) {
        this.task = res
        this.name = res.name
        this.startTime = res.startTime
        this.endTime = res.endTime
        this.content = res.content
        this.target = res.target
        this.leaderName = res.leaderName
        this.projectName = res.projectName
        this.projectID = res.projectID
        this.getProjectState(res.projectID)
      }
    },
    getMissionSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.editMissionForm = res
        this.editMissionForm.state = res.state
        this.editMission = true
      } else alert(res.data)
    },
    getMissionList () {
      axios.get(this.url + '/mission/module/' + sessionStorage.getItem('taskID'))
        .then(this.getMissionListSucc)
    },
    getMissionListSucc (res) {
      res = res.data
      if (res) {
        this.missionList = res
      }
    },
    getStudentList () {
      axios.get(this.url + '/user/level/2')
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      console.log('getStudentListSucc')
      if (res.status === 200) {
        res = res.data
        this.studentList = res
        console.log(this.studentList)
      }
    },
    submitMission () {
      if (this.mission.ownerID === -1) {
        this.$notify.warning({
          title: '请选择任务负责人！'
        })
        return
      }
      if (this.mission.name === '') {
        this.$notify.warning({
          title: '请输入任务名称！'
        })
        return
      }
      if (this.mission.name.length > 10) {
        this.$notify.warning({
          title: '任务名称不能超过十个字！'
        })
        return
      }
      this.$axios({
        method: 'post',
        url: this.url + '/mission/',
        data: this.mission
      }).then((res) => {
        if (res.status === 200) {
          // res = res.data
          alert('提交成功！')
          this.getMissionList()
          // this.missionList.push({
          //   name: this.mission.name,
          //   ownerName: res.ownerName
          // })
        } else alert('提交失败！请重新打开页面')
      }).catch((err) => {
        alert(err)
      })
      this.addMission = false
    },
    updateMission () {
      if (this.editMissionForm.ownerID === -1) {
        this.$notify.warning({
          title: '请选择任务负责人！'
        })
        return
      }
      if (this.editMissionForm.name === '') {
        this.$notify.warning({
          title: '请输入任务名称！'
        })
        return
      }
      this.$axios({
        method: 'put',
        url: this.url + '/mission/',
        data: this.editMissionForm
      }).then((res) => {
        if (res.status === 200) {
          // res = res.data
          alert('提交成功！')
        } else alert(res.data)
      }).catch((err) => {
        alert(err)
      })
      this.editMission = false
    },
    submit () {
      this.$axios({
        method: 'put',
        url: this.url + '/module/',
        data: {
          id: parseInt(sessionStorage.getItem('taskID')),
          state: parseInt(this.taskState),
          leaderID: this.task.leaderID,
          name: this.name,
          startTime: this.startTime,
          endTime: this.endTime,
          content: this.content,
          target: this.target
        }
      }).then((res) => {
        if (res.status === 200) alert('提交成功！')
        else alert('提交失败！请重新打开页面')
        this.okTask = false
        this.$router.push('/check')
      }).catch((err) => {
        alert(err)
      })
    },
    getProjectState (projectId) {
      console.log('pid' + projectId)
      axios.get(this.url + '/project/id/' + projectId)
        .then(this.getProjectStateSucc)
    },
    getProjectStateSucc (res) {
      if (res.status === 200) {
        res = res.data
        if (res.state === this.Run) this.taskState = this.Run
        else this.taskState = this.ProjectWaitForCheck
      }
    },
    delMission (chooseMissionId) {
      this.$axios({
        method: 'delete',
        url: this.url + '/mission/id/' + chooseMissionId
      }).then((res) => {
        if (res.status === 200) {
          alert('删除成功！')
          this.chooseOperation = false
          axios.get(this.url + '/mission/module' + chooseMissionId)
            .then(this.getMissionListSucc)
          this.getMissionList()
        }
      }).catch((err) => {
        alert(err)
      })
    },
    handleEditMission (chooseMissionId) {
      this.editMission = true
      axios.get(this.url + '/mission/id/' + chooseMissionId)
        .then(this.getMissionSucc)
    }
  },
  mounted () {
    this.getTask()
    this.getMissionList()
    this.getStudentList()
  }
}



